@font-face {
  font-family: "Roboto Slab";
  src: url("../fonts/RobotoSlabRegular.woff2") format("woff2"), url("../fonts/RobotoSlabRegular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Roboto Slab";
  src: url("../fonts/RobotoSlabBold.woff2") format("woff2"), url("../fonts/RobotoSlabBold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

@font-face {
  font-family: "Work Sans";
  src: url("../fonts/WorkSansRegular.woff2") format("woff2"), url("../fonts/WorkSansRegular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Work Sans";
  src: url("../fonts/WorkSansBold.woff2") format("woff2"), url("../fonts/WorkSansBold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

html {
  scroll-behavior: smooth; }

body {
  font-family: "Work Sans", "Arial", sans-serif;
  box-sizing: border-box; }

img {
  width: 100%;
  height: auto; }

p {
  margin: 0; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  display: flex;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: inherit;
  justify-content: center;
  align-items: center; }

.body-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #131313;
  margin: 0 auto; }

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 375px;
  margin: 0 auto;
  background-color: #FAFAFA; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

::-webkit-scrollbar {
  width: 3px; }

::-webkit-scrollbar-thumb {
  background: #EE3D48;
  border-radius: 50px; }

::-webkit-scrollbar-thumb:hover {
  opacity: 0.7; }

.main-header {
  padding-top: 10px;
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 30px; }

.main-body__video-wrapper {
  background-image: url("../img/dots_background.svg"), url("../img/circle_background.svg");
  background-repeat: no-repeat;
  background-size: 82px 44px, 125px 125px;
  background-position: 287px 6px, -7px 151px;
  padding: 0 16px;
  padding-top: 17px;
  margin-bottom: 20px;
  box-sizing: border-box;
  width: 100%;
  min-height: 253px; }

.main-body__video-block {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 200px;
  border: 3px solid #EE3D48;
  border-radius: 8px;
  box-sizing: border-box;
  cursor: pointer; }
  .main-body__video-block iframe {
    border-radius: 8px; }

.features-block {
  padding: 0 16px;
  margin-bottom: 80px; }

.features-block__title {
  font-family: "Roboto Slab", "Arial", sans-serif;
  font-weight: bold;
  font-size: 40px;
  line-height: 53px;
  margin: 0;
  margin-bottom: 20px; }
  .features-block__title-red {
    color: #EE3D48; }

.features-block__text {
  font-family: "Work Sans", "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 21px;
  margin-bottom: 20px; }
  .features-block__text-red {
    color: #EE3D48;
    text-decoration: underline; }

.features-block__list {
  display: flex;
  flex-wrap: wrap; }

.features-block__item {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  width: 156px;
  min-height: 84px;
  margin-right: 16px;
  margin-bottom: 35px;
  font-family: "Work Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  background-repeat: no-repeat; }
  .features-block__item:nth-child(2n) {
    margin-right: 0; }
  .features-block__item:nth-last-of-type(-n + 2) {
    margin-bottom: 0; }
  .features-block__item p {
    width: 143px; }
  .features-block__item--width p {
    width: 124px; }
  .features-block__item--infinity {
    background-image: url("../img/infinity.png");
    background-position: 58px 17px; }
  .features-block__item--mustache {
    background-image: url("../img/mustaches.png");
    background-position: 58px 0px; }
  .features-block__item--photos {
    background-image: url("../img/photos.png");
    background-position: 58px 17px; }
  .features-block__item--prices {
    background-image: url("../img/price_label.png");
    background-position: 58px 17px; }

.photo-booth__title {
  padding: 0 16px; }

.photo-booth__filter-wrapper {
  padding: 0 16px; }

.photo-booth__item {
  background-color: #ffffff;
  padding: 0 16px; }

.photo-booth__slider {
  width: 292px;
  min-height: 222px; }

.extra-options {
  min-height: 166px;
  margin-bottom: 10px; }

.extra-options__title {
  font-family: "Work Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  color: #EE3D48;
  border-bottom: 1px solid #EE3D48;
  width: 80px;
  min-height: 22px;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center; }

.extra-options__list {
  height: 152px;
  overflow-y: auto; }

.extra-options__item {
  background-color: #FAFAFA;
  border-bottom: 1px solid #EBEBEB; }
  .extra-options__item:last-child {
    border-bottom: none; }

.extra-options__item-label {
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  min-height: 60px;
  padding: 8px;
  padding-right: 6px; }
  .extra-options__item-label:hover input:not(:checked) ~ .extra-option__item-custom-checkbox {
    opacity: 0.7; }
  .extra-options__item-label input:checked ~ .extra-option__item-custom-checkbox {
    background-color: #EE3D48;
    background-image: url("../img/tick.png");
    border: 0; }

.extra-options__item-image {
  width: 60px;
  height: 60px;
  object-fit: cover; }

.extra-options__text-wrapper {
  margin-top: 6px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 6px;
  width: 100%; }

.extra-options__item-title {
  margin: 0;
  margin-bottom: 12px;
  font-family: "Work Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px; }

.extra-options__item-price {
  font-family: "Roboto Slab", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #EE3D48; }

.extra-option__item-checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }

.extra-option__item-custom-checkbox {
  position: absolute;
  box-sizing: border-box;
  top: 31px;
  right: 8px;
  height: 15px;
  width: 15px;
  border: 1px solid #EE3D48;
  border-radius: 2px; }

.rent-time__title {
  font-family: "Work Sans", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #939598;
  margin: 0;
  margin-bottom: 8px; }

.rent-time__list {
  display: flex;
  flex-wrap: wrap; }

.rent-time__item {
  min-height: 18px;
  padding: 4px 12px;
  margin-right: 8px;
  background-color: #EE3D48;
  border-radius: 6px; }
  .rent-time__item:last-child {
    margin-right: 0; }
